import React from "react";
import DataTable from "react-data-table-component";
import { useFetch } from "usehooks-ts";
import { Attendee } from "./Attendee";
import styled from "styled-components";
import "./styles.css";
import { ExpandedComponent } from "./ExpandedComponent";
import * as R from "ramda";
import Select from "react-select";

const columns = [
  {
    name: "Name",
    selector: (d: Attendee) => d.name,
    sortable: true
  },
  {
    name: "Company",
    selector: (d: Attendee) => d.company,
    sortable: true
  },
  {
    name: "Title",
    selector: (d: Attendee) => d.job_title
  },
  {
    name: "Categories",
    selector: (d: Attendee) => d.business_category.join(", ")
  }
];

const TextField = styled.input`
  height: 36px;

  width: calc(100%-32px);

  flex-grow: 1;

  border-radius: 3px;

  border-top-left-radius: 5px;

  border-bottom-left-radius: 5px;

  border-top-right-radius: 0;

  border-bottom-right-radius: 0;

  border: 1px solid #e5e5e5;

  padding: 0 32px 0 16px;

  &:hover {
    cursor: pointer;
  }
`;

const ClearButton = styled.button`
  border-top-left-radius: 0;

  border-bottom-left-radius: 0;

  border-top-right-radius: 5px;

  border-bottom-right-radius: 5px;

  height: 36px;

  width: 32px;

  text-align: center;

  display: flex;

  align-items: center;

  justify-content: center;
`;

const HeaderDiv = styled.div`
  min-width: 300px;
  width: 25%;
  flex-grow: 1;
`;

const FilterComponent = ({
  filterText,
  onFilter,
  onClear
}: {
  filterText: string;
  onFilter: (event: React.ChangeEvent<HTMLInputElement>) => void;
  onClear: () => void;
}) => (
  <div style={{ display: "flex" }}>
    <TextField
      id="search"
      type="text"
      placeholder="Filter By Name or Company"
      aria-label="Search Input"
      value={filterText}
      onChange={onFilter}
    />

    <ClearButton type="button" onClick={onClear}>
      X
    </ClearButton>
  </div>
);

function RenderAttendees({ data }: { data: Attendee[] }) {
  const [filterText, setFilterText] = React.useState("");
  const [filterCategories, setFilterCategories] = React.useState<Set<string>>(
    new Set([])
  );
  const [filterServices, setFilterServices] = React.useState<Set<string>>(
    new Set([])
  );
  const [filterMarkets, setFilterMarkets] = React.useState<Set<string>>(
    new Set([])
  );

  const [resetPaginationToggle, setResetPaginationToggle] = React.useState(
    false
  );

  const categories = React.useMemo(() => {
    return R.pipe(
      R.chain((x: Attendee) => x.business_category),
      R.uniq,
      R.filter((x: string) => x.length > 0),
      R.map((c) => ({ label: c, value: c }))
    )(data);
  }, [data]);

  const services = React.useMemo(() => {
    return R.pipe(
      R.chain((x: Attendee) => x.services),
      R.uniq,
      R.filter((x: string) => x.length > 0),
      R.map((c) => ({ label: c, value: c }))
    )(data);
  }, [data]);

  const markets = React.useMemo(() => {
    return R.pipe(
      R.chain((x: Attendee) => x.markets),
      R.uniq,
      R.filter((x: string) => x.length > 0),
      R.map((c) => ({ label: c, value: c }))
    )(data);
  }, [data]);

  const filteredItems = data.filter(
    (item: Attendee) =>
      (item.name.toLowerCase().includes(filterText.toLowerCase()) ||
        item.company.toLowerCase().includes(filterText.toLowerCase())) &&
      (filterCategories.size === 0 ||
        item.business_category.some((c) => filterCategories.has(c))) &&
      (filterServices.size === 0 ||
        item.services.some((c) => filterServices.has(c))) &&
      (filterMarkets.size === 0 ||
        item.markets.some((c) => filterMarkets.has(c)))
  );

  const subHeaderComponentMemo = React.useMemo(() => {
    const handleClear = () => {
      if (filterText) {
        setResetPaginationToggle(!resetPaginationToggle);

        setFilterText("");
      }
    };

    return (
      <div
        style={{
          display: "flex",
          flexWrap: "wrap",
          rowGap: 10,
          columnGap: 10
        }}
      >
        <HeaderDiv>
          <Select
            placeholder="Categories"
            options={categories}
            isMulti
            onChange={(values) => {
              setFilterCategories(new Set(values.map((e) => e.value)));
            }}
          ></Select>
        </HeaderDiv>
        <HeaderDiv>
          <Select
            placeholder="Servcies"
            options={services}
            isMulti
            onChange={(values) => {
              setFilterServices(new Set(values.map((e) => e.value)));
            }}
          ></Select>
        </HeaderDiv>

        <HeaderDiv>
          <Select
            placeholder="Markets"
            options={markets}
            isMulti
            onChange={(values) => {
              setFilterMarkets(new Set(values.map((e) => e.value)));
            }}
          ></Select>
        </HeaderDiv>
        <HeaderDiv>
          <FilterComponent
            onFilter={(e) => setFilterText(e.target.value)}
            onClear={handleClear}
            filterText={filterText}
          />
        </HeaderDiv>
      </div>
    );
  }, [
    filterText,
    resetPaginationToggle,
    setFilterCategories,
    markets,
    services,
    categories
  ]);

  return (
    <DataTable
      columns={columns}
      data={filteredItems}
      expandableRows={true}
      expandableRowsComponent={ExpandedComponent}
      expandableRowExpanded={() => !true}
      subHeader
      subHeaderComponent={subHeaderComponentMemo}
    />
  );
}

export default function App() {
  const { data, error } = useFetch<Attendee[]>("./attendees.json");

  if (error) return <p>There is an error.</p>;
  if (!data) return <p>Loading...</p>;

  return (
    <div>
      <RenderAttendees data={data} />
    </div>
  );
}
